// 
export default function matrixTransform(list, temp, type) {
  // 1 90
  const tempList = [...temp];
  if (type === 1) {
    for (let i = 0; i < 4; i++) {
      for (let j = 0; j < 4; j++) {
        tempList[i][j] = list[j][3 - i];
      }
    }
    return [...tempList];
  }

  // 2 90
  if (type === 2) {
    for (let i = 0; i < 4; i++) {
      for (let j = 0; j < 4; j++) {
        tempList[j][i] = list[3 - i][j];
      }
    }
    return [...tempList];
  }

  return null;
}



